import React from 'react';
import shortid from 'shortid';
import style from './newsListStyle.module.css';

const NewsList = ( { imgList, openModal } ) => (
    <ul className={style.list}>
        {
            imgList.map( el => (
                <li onClick={openModal} key={shortid.generate()} className={style.newsItem}>
                    {el.author ? <h3>{el.author}</h3> : <h3>We dont have info about author</h3>}
                    <img className={style.img} src={el.urlToImage} alt="news img" />
                    {el.content ? <p>{el.content}</p> : <p>NOTHING</p>}
                    <span>{el.publishedAt}</span>
                </li>
            ) )
        }
    </ul>
)

export default NewsList;